:root {
  --font-body: "Rubik", sans-serif;
  --fontsize-body: 1.125rem;
}

:root {
  --softblue: hsl(231, 69%, 60%);
  --softred: hsl(0, 94%, 66%);
  --grayishblue: hsl(229, 8%, 70%);
  --verylightgray: hsl(0, 0%, 75%);
  --dimgray: hsl(0, 0%, 41%);
  --verydarkblue: 229 31% 21%;
  --white: hsl(100, 100%, 100%);
  --black: hsl(0, 0%, 0%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-body);
  font-size: var(--fontsize-body);
  background-color: var(--white);
  font-weight: 300;
}

h1 {
  margin-top: 0;
  line-height: 1.1;
  font-weight: 500;
  font-size: 1.75rem;
  text-align: center;
}

h2 {
  margin-top: 0;
  line-height: 1.1;
  font-weight: 500;
  font-size: 1.5rem;
  text-align: center;
}

h3 {
  margin-top: 0;
  line-height: 1.1;
  font-weight: 500;
  font-size: 1.25rem;
  text-align: center;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

p {
  color: var(--grayishblue);
  text-align: center;
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.5;
  margin: 0;
}

.container {
  padding: 0 2rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

@media (min-width: 71.875em) {
  .hide-for-desktop {
    display: none;
  }
}

@media (max-width: 71.8125em) {
  .hide-for-mobile {
    display: none;
  }
}

.header__nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2.5rem 0;
}
@media (min-width: 71.875em) {
  .header__nav--links {
    display: flex;
    align-items: center;
    gap: 2.875rem;
    text-transform: uppercase;
    color: black;
    font-weight: 400;
    font-size: 0.875rem;
    letter-spacing: 0.125rem;
  }
  .header__nav--links a {
    color: var(--black);
  }
  .header__nav--links a:hover {
    color: var(--softred);
    transition: color 300ms ease-in;
  }
}
@media (min-width: 71.875em) {
  .header__nav--button {
    background-color: var(--softred);
    color: var(--white);
    padding: 0.75rem 0;
    width: 7.8125rem;
    text-align: center;
    border-radius: 0.3125rem;
    box-shadow: 0rem 0.1875rem 0.75rem rgba(0, 0, 0, 0.2);
    border: 0.125rem solid var(--softred);
  }
  .header__nav--button:hover {
    background-color: var(--white);
    color: var(--softred);
    transition: all 300ms ease-in-out;
  }
}

.mobileMenu {
  position: fixed;
  z-index: 1;
  right: 0;
  top: 0;
  height: 100vh;
  width: 100vw;
  padding: 2.5rem 2rem;
  background: hsl(var(--verydarkblue)/0.95);
  transform: translateX(100%);
  transition: transform 700ms ease-in-out;
}
.mobileMenu__nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.mobileMenu__links {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1.875rem 0;
}
.mobileMenu__links a {
  width: 100%;
  text-align: center;
  text-transform: uppercase;
  color: var(--verylightgray);
  font-size: 1.125rem;
  font-weight: 400;
  border-top: 0.0625rem solid var(--dimgray);
  letter-spacing: 0.0625rem;
  padding: 1.4375rem 0;
}
.mobileMenu__links a :last-child {
  color: var(--white);
}
.mobileMenu__links a:hover {
  color: var(--white);
  transition: all 300ms ease-in-out;
}
.mobileMenu__socials {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2.5rem;
  padding-top: 4.375rem;
}
.mobileMenu__socials a {
  position: relative;
}
.mobileMenu__socials a::before {
  content: "";
  display: block;
  position: absolute;
  height: 0.125rem;
  width: 100%;
  left: 0;
  right: 1.5rem;
  bottom: -0.5rem;
  background: #fff;
  opacity: 0;
  transition: opacity 200ms ease-in-out;
}
.mobileMenu__socials a:hover::before {
  opacity: 1;
}
.mobileMenu__login-button {
  border: 0.125rem solid var(--white);
  border-radius: 0.3125rem;
  padding: 0.5rem 0;
}

.mobileMenu__open {
  transform: translateX(0%);
}

.mobileMenu__noScroll {
  overflow-y: hidden;
}

@media (min-width: 71.875em) {
  .hero {
    overflow-x: hidden;
  }
}
.hero__container {
  overflow-x: hidden;
  padding-top: 40px;
}
@media (min-width: 71.875em) {
  .hero__container {
    display: flex;
    align-items: center;
    overflow-x: visible;
  }
}
.hero__image {
  position: relative;
  height: 266px;
}
@media (min-width: 40em) {
  .hero__image {
    height: 532px;
  }
}
@media (min-width: 71.875em) {
  .hero__image {
    flex: 3;
    order: 2;
  }
}
.hero__image::before {
  content: "";
  position: absolute;
  top: 18%;
  right: 0;
  bottom: 0;
  left: 12%;
  width: 150%;
  background-color: var(--softblue);
  border-radius: 165px;
}
.hero__image::after {
  content: "";
  position: absolute;
  top: -4%;
  right: -1%;
  bottom: -10%;
  left: -6%;
  background-image: url(../../../images/illustration-hero.svg);
  background-repeat: no-repeat;
  background-size: 107% 86%;
}
@media (min-width: 40em) {
  .hero__image::after {
    background-size: 106% 96%;
    bottom: 0;
    left: -7%;
    right: 0;
    top: 0;
  }
  .hero__image::before {
    border-radius: 180px;
    bottom: 0;
    top: 23%;
  }
}
@media (min-width: 71.875em) {
  .hero__image::after {
    background-size: 86% 90%;
    bottom: 0;
    left: 12%;
    right: 0;
    top: 0;
    width: 126%;
  }
  .hero__image::before {
    border-radius: 200px;
    bottom: 6%;
    top: 28%;
    left: 45%;
  }
}
.hero__text {
  padding: 60px 0;
}
@media (min-width: 71.875em) {
  .hero__text {
    flex: 2;
    order: 1;
  }
  .hero__text h1 {
    font-size: 40px;
    text-align: start;
  }
  .hero__text p {
    text-align: start;
  }
}
.hero__text--buttons {
  padding: 40px 0;
  display: flex;
  justify-content: center;
  gap: 11px;
}
@media (min-width: 71.875em) {
  .hero__text--buttons {
    gap: 25px;
    justify-content: flex-start;
  }
}

.button-chrome {
  color: var(--white);
  background-color: var(--softblue);
  padding: 0.9375rem 1.0625rem;
  border-radius: 0.3125rem;
  font-size: 0.875rem;
  font-weight: 500;
  box-shadow: 0rem 0.1875rem 0.75rem rgba(0, 0, 0, 0.2);
  border: 2px solid var(--softblue);
}
.button-chrome:hover {
  background-color: var(--white);
  color: var(--softblue);
  transition: all 300ms ease-in-out;
}
@media (min-width: 71.875em) {
  .button-chrome {
    padding: 0.75rem 1.25rem;
  }
}

.button-firefox {
  color: var(--black);
  background-color: #f2f2f2;
  padding: 0.9375rem 1.0625rem;
  border-radius: 0.3125rem;
  font-size: 0.875rem;
  font-weight: 500;
  box-shadow: 0rem 0.1875rem 0.75rem rgba(0, 0, 0, 0.2);
  border: 2px solid #f2f2f2;
}
.button-firefox:hover {
  border-color: var(--black);
  background-color: var(--white);
  transition: all 300ms ease-in-out;
}
@media (min-width: 71.875em) {
  .button-firefox {
    padding: 0.75rem 1.25rem;
  }
}

.feature__container {
  padding-top: 4.25rem;
  padding-bottom: 4.25rem;
}
.feature__text {
  padding-bottom: 2.5rem;
}
.feature__bookmarks--buttons {
  display: flex;
  flex-direction: column;
  cursor: pointer;
}
.feature__bookmarks--button {
  position: relative;
  padding: 1.25rem 0;
  font-weight: 500;
  text-align: center;
  color: var(--grayishblue);
  border-top: 2px solid #f0f0f0;
  border-bottom: 2px solid #f0f0f0;
}
.feature__bookmarks--button:nth-child(2) {
  border: none;
}

.has-underline {
  color: black;
  transition: color 400ms ease-in-out;
}
.has-underline::before {
  content: "";
  display: block;
  position: absolute;
  height: 4px;
  width: 50%;
  left: 25%;
  right: 0;
  bottom: 0;
  background: var(--softred);
}